

.phoneCard p {
  margin-bottom: 3px;
  font-size: 12px;
}
.phoneCard p.CompanyName {
  font-weight: bold;
}
.ant-layout-content {
  width: 1200px;
}
.ant-layout-content.headerContent {
  margin: 0 auto;
}
.ant-layout-content.pageContent {
  margin: 13px auto 30px;
  padding: 30px;
  background-color: #fff;
}
.ant-layout-content.breadContent {
  margin: 15px auto 0;
}
@media (max-width: 760px) {
  .ant-layout-content {
    width: 100%;
  }
  .ant-col-8 {
    width: 100%;
  }
  .ant-layout-content.breadContent {
    padding: 0 10px;
  }
  .ant-layout-content.pageContent {
    padding: 0;
  }
  .ant-layout-header {
    padding: 0 15px;
  }
}